//aside-menu

.aside-mail {
	padding: 20px 0 10px 0px;

	a {
		color: #57626a;
		font-size: 15px;
		display: block;
		margin-bottom: 7px;

		span {
			color: #095fa1;
			font-weight: 700;
		}
	}
}

.aside-menu {
	padding-bottom: 7px;

	.active > .drop-menu {
		display: block;
	}

	a {
		color: #57626a;
		font-size: 15px;
			padding-left: 14px;
	}

	li {
		position: relative;

		&:before {
			@extend %pseud;
		}
		&.active > a,
		&:hover > a {
			color: #005b92;
		}
	}


	> li {
		margin-bottom: 21px;

		&:before {
			width: 6px;
			height: 11px;
			background: url(../img/sprite.png) 0 -11px;
			top: 4px;
		}

		&:hover,
		&.active {
			&:before {
				background-position: -6px -11px;
			}
		}
	}
}

.drop-menu {
	padding-top: 15px;
	display: none;

	a {
		padding-left: 26px;
	}

	> li {
		margin-bottom: 9px;

		&:before {
			width: 5px;
			height: 9px;
			background: url(../img/sprite.png) -12px -11px;
			top: 7px;
			left: 14px;
		}

		&:hover,
		&.active {
			&:before {
				background-position: -17px -11px;
			}
		}
	}

	.drop-menu-second {
		padding-top: 9px;

		li {
			margin-bottom: 5px;
			&:before {
				top: 7px;
				left: 31px;
				background-position: -23px -11px;
			}
		}

		a {
			font-size: 13px;
			padding-left: 40px;
		}

		.active,
		li:hover {
			color: #e76200;

			&:before {
				background-position: -28px -11px;
			}
		}
	}

}


//express-menu

.express-menu {
	background: #fafafa;
	border: 1px solid #fff;
	box-shadow: 0px 0px 0px 1px #d3d1d1;

	.aside-menu {
		padding: 16px 7px 0;
	}
}

.menu-title {
	background: #095fa1;
	color: #fff;
	font-size: 15px;
	font-weight: 700;
	padding: 8px 5px 8px 7px;
}
