/* ==========================================================================
*	объеденить CENTER CENTERT
*
*
*
*
========= */

@mixin center {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
}

@mixin centerT {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

@mixin absolute($absolute, $top, $right, $bottom, $left) {
	position: $absolute;
	top: $top;
	right: $right;
	bottom: $bottom;
	left: $left;
}

/*
 *	@include calc('100% - 391px');
 */
@mixin calc($size) {
	width: -webkit-calc(#{$size});
	width: -moz-calc(#{$size});
	width: calc(#{$size});
}

/*
 *	@include placeholder{color:rgba(255,0,0,.8);}
 */
@mixin placeholder {
	&::-webkit-input-placeholder {@content}
	&:-moz-placeholder           {@content}
	&::-moz-placeholder          {@content; opacity: 1;}
	&:-ms-input-placeholder      {@content}
}


/* ==========================================================================
font
========= */

@mixin font($weight, $size) {
	$size: $size/16;
	font: $weight $size + em $font-family;
}

@mixin em($size) {
	$size: $size/16;
	font-size: $size + em;
}

// BACKGROUND
@mixin background($name, $color:false){
	@if($color){
		background: $color;
	} @else {
		background: url(#{$img-path}#{$name});
	}
}

// Бордер без бекграунда при прозрачности
@mixin borde {
	background-clip: border-box;
	background-clip: padding-box;
	background-clip: content-box;
}

/* CLEARFIX
 * .con {
 * 		@extend %clfix;
 * }
 */
%clfix {
	*zoom: 1;
	&:before, &:after {
		content: " ";
		display: table;
	}
	&:after {
		clear: both;
	}
}

/* MARGIN INLANE BLOC -.3EM
 * .con {
 *		@extend %marinl;
 * }
 */
%marinl {
	margin-left: -.36em;

	&:first-child {
		margin-left: 0;
	}
}

/* PSEUDOS
 * .con {
 *		@extend %pseud;
 * }
 */

%pseud {
	content: '';
	position: absolute;
}

%hide {
	display: inline-block;
	overflow: hidden;
	text-indent: 100%;
	color: transparent;
	white-space: nowrap;
}


//

@mixin reset {
	padding: 0;
	margin: 0;
}

@mixin reset-list {
	list-style-type: none;
	@include reset;
}

// OPACITY IE8
@mixin opacity($opacity) {
	opacity: $opacity;
	$opacity-ie: $opacity * 100;
	filter: alpha(opacity=$opacity-ie); //IE8
}



//-----------gradient---------------
@mixin background-gradient($start-color, $end-color, $orientation) {
	background: $start-color;

	@if $orientation == 'vertical' {
		background: -webkit-linear-gradient(top, $start-color, $end-color);
		background: linear-gradient(to bottom, $start-color, $end-color);
	} @else if $orientation == 'horizontal' {
		background: -webkit-linear-gradient(left, $start-color, $end-color);
		background: linear-gradient(to right, $start-color, $end-color);
	} @else {
		background: -webkit-radial-gradient(center, ellipse cover, $start-color, $end-color);
		background: radial-gradient(ellipse at center, $start-color, $end-color);
	}
}

//-------------font face--------------------
@mixin font-face($font-name, $file-name) {
	@font-face {
		font-family: quote($font-name);
		src: url('assets/fonts/'+ $file-name + '.eot');
		src: url('assets/fonts/'+ $file-name + '.eot?#iefix')  format('embedded-opentype'),
					 url('assets/fonts/'+ $file-name + '.woff') format('woff'),
					 url('assets/fonts/'+ $file-name + '.ttf')  format('truetype'),
					 url('assets/fonts/'+ $file-name + '.svg##{$font-name}')  format('svg');
		font-weight: normal;
		font-style: normal;
	}
}
