td span.active {
		color: #8fac44;
}

td .check-box,
th .check-box {
	overflow: hidden;
	text-indent: -9999px;

	input:checked + label:after {
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
		margin: auto;
	}

	label {
		padding: 0;
		margin: 0;

		&:before {
			left: 0;
			top: 0;
			bottom: 0;
			right: 0;
			margin: auto;
		}

		&:after {
			left: 0;
			top: 0;
			bottom: 0;
			right: 0;
			margin: auto;
		}
	}
}

.table {
	margin-bottom: 36px;
	@extend %clfix;

	a {
		color: #095fa1;
	}

	.col-sort {
		text-align: left;
		padding-left: 12px;
	}

	select,
	.select2 {
		float: right;
		width: 189px;
	}

	h1,
	.styleh1 {
		float: left;
	}

	table {
		width: 100%;
		table-layout: fixed;
	}

	th {
		background: #095fa1;
		color: #fff;
		font-size: 13px;
		height: 30px;
	}

	td {
		color: #57626a;
		font-size: 14px;
		height: 31px;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	tbody {

		tr {
			&:nth-child(odd) {
				background: #fafafa;
			}
			&:nth-child(even) {
				background: #f1f1f1;
			}
		}
	}

	tr {
		border-bottom: 1px solid #fff;
	}

	th,
	td {
		text-align: center;
		opacity: 1;

		&:not(:last-child) {
			border-right: 1px solid #fff;
		}
	}

	tbody {
		tr {

			&:last-child {
				border-bottom: 0px solid #fff;
			}
		}
	}

	tbody:hover {


		td {
			opacity: .5;
		}
		tr:hover {
			td {
				opacity: 1;
			}
		}

	}


}

