.table-sort {
	margin-bottom: 6px;

	th {
		position: relative;

		i {
			position: absolute;
			display: block;
			width: 0;
			height: 0;
			@extend %hide;
			right: 10px;
			border-style: solid;
			opacity: .5;
			cursor: pointer;
			transition: opacity .2s linear;

			&:hover,
			&.active {
				opacity: 1,
			}
			&:first-child {
				top: 7px;
				border-width: 0 5.5px 6px 5.5px;
				border-color: transparent transparent #fff transparent;

			}
			&:last-child {
				bottom: 8px;
				border-width: 6px 5.5px 0 5.5px;
				border-color: #fff transparent transparent transparent;
			}
		}
	}
}

.tabl-report,
.tabl-registration {
	padding-bottom: 20px;


	tr {
		height: 31px;
	}

	thead {
		background: #095fa1;
		color: #fff;
		font-size: 13px;
		text-align: left;

		th {

			&:first-child {
				padding-left: 20px;
			}
			&:not(:first-child) {
				text-align: center;
			}
			&:not(:last-child) {
				border-right: 1px solid #fff;
			}
		}

	}

	tbody {

		tr {
			font-size: 14px;
			border-top: 1px solid #fff;

			&:first-child {
				background: #277bbc;
				color: #fff;
			}
			&:nth-child(even) {
				background: #ededed;
			}
			&:nth-child(n+3):nth-child(odd) {
				background: #fafafa;
			}
		}

		td {
			&:not(:last-child) {
				border-right: 1px solid #fff;
			}
			&:first-child {
				padding-left: 20px;
			}
			&:nth-child(2) {
				text-align: center;
			}
		}
	}
}




