img {
	vertical-align: middle;
	border: 0;
}

html{
	height: 100%;
}

body {
	min-height: 100%;
	position: relative;
	padding-bottom: 97px;
}

.container {
	margin-right: auto;
	margin-left: auto;
	padding-left: 10px;
	padding-right: 10px;
	width: 1280px;
}

.container-fluid {
	margin-right: auto;
	margin-left: auto;
	padding-left: 10px;
	padding-right: 10px;
}

.row {
	margin-left: -5px;
	margin-right: -5px;
	@extend %clfix;
}

.col {
	position: relative;
	min-height: 1px;
	margin-left: 5px;
	margin-right: 5px;

	&-16 {
		width: 1000px;
		float: left;
	}
	&-4 {
		width: 250px;
		float: left;
	}
	&-20 {
		width: 1260px;
	}
}

a,
a:hover,
a:focus {
	text-decoration: none;
}

:focus {
	outline: none;
}

ul, li, ol {
	list-style: none;
	padding: 0;
	margin: 0;
}

li {
	list-style-type: none;
}

body {
	font-smoothing: antialiased;
}

* {
	box-sizing: border-box;
}

a {
	text-decoration: none;
}

h1,
h2,
h3 {
	margin: 0;
	padding: 0;
}

p {
	margin: 0;
}

