// основные

body {
	font-family: Tahoma, Geneva, sans-serif;
}

.fon-line {
	background: url(../img/fon-line.jpg);
	background-repeat: repeat-x;
	min-width: 1280px;
	width: 100%;

	&-head {background-position: center -4px;}
	&-foot {background-position: center 0px;}
}

footer {
	min-width: 1280px;
	width: 100%;
	height: 97px;
	bottom: 0;
	left: 0;
	position: absolute;
}

h1,
.styleh1 {
	color: #004f81;
	font-weight: 700;
	font-size: 21px;
	margin-bottom: 17px;
}

main {
	padding-top: 42px;
	padding-bottom: 100px;
}



.dotted,
.dotted-top {
	position: relative;

	&:before {
		@extend %pseud;
		background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAcAAAABCAYAAAASC7TOAAAAHUlEQVQIW2M8f/na//1HTzHAgJS4KENEoDcjiA8Amn4HAjBsvEYAAAAASUVORK5CYII=) repeat;
		height: 1px;
		bottom: 0;
		width: 100%;
	}
}

.dotted-top {
	&:before {
		top: 0;
	}
}

.dotted {

	&:before {
		bottom: 0;
	}
}

aside {
	section {
		&:not(:last-child) {
			position: relative;

			&:before {
				@extend %pseud;
				background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAcAAAABCAYAAAASC7TOAAAAHUlEQVQIW2M8f/na//1HTzHAgJS4KENEoDcjiA8Amn4HAjBsvEYAAAAASUVORK5CYII=) repeat;
				height: 1px;
				bottom: 0;
				width: 100%;
			}
		}
	}
}


//footer
footer {
	margin-top: 30px;
	padding-top: 24px;

	p {
		color: #7ea0bb;
		font-size: 14px;

		span {
			color: #ffffff;
		}
	}
}

.hidden-box {
	display: none;
}
