.btn {
	background: #004f81;
	color: #ffffff;
	font-size: 15px;
	background-image: url(../img/bg-fon.png);
	transition: all .2s linear;

	&.btn-add,
	&.btn-ready {
		border: 3px solid #ccdce6;
		border-radius: 3px;
		padding: 5px 50px 7px 50px;
		position: relative;
		overflow: hidden;
		width: 238px;

		&.btn-newlink {
			padding: 5px 5px 7px 5px;
			margin-top: 22px;
		}

		&:hover {
		  background: #2381bc;
		}

		&.btn-2 {
			width: 208px;
		}
		&.btn-3 {
			width: 208px;
			padding: 5px 5px 7px;
		}
		&.btn-4 {
			width: 250px;
			padding: 5px 0px 7px;
		}
		&.btn-5 {
			width: 188px;
			padding: 5px 0px 7px;
		}
		&.btn-r {
			float: right;
		}
		&.block {
			display: block;
		}

		&.btn-new {
			width: 100%;
			margin-top: 12px;
		}
	}

}
