// меню

nav {

	> ul {
		display: table;
		width: 100%;
		border-spacing: 1px;

		> li {
			display: table-cell;

			> a {
				text-align: center;
				display: block;
				background: #0c304c;
				padding: 12px;
				border: 4px solid #064067;
				color: #b6d2e8;
				font-size: 15px;
				font-weight: 700;
				transition: all .3s ease;
			}

			&:not(:last-child) {
				margin-right: 1px;
			}

			&.active,
			&:hover {

				> a {
					background: #e76200;
					border: 4px solid #eb7926;
					color: #fff;
				}
			}

			&:hover	> ul {
				top: 50px;
				opacity: 1;
				visibility: visible;
			}
		}
	}
}

.dropdown-top {
	position: relative;

	> ul {
		width: 100%;
		min-width: 230px;
	}

	ul {
		top: 100px;
		background: #e76200;
		position: absolute;
		z-index: 50;
		transition: all .3s ease;
		opacity: 0;
		visibility: hidden;
		padding-top: 5px;
		padding-bottom: 5px;

		li {
			padding-left: 5px;
			padding-right: 5px;

			&:hover > a {
				background: #ba3e1c;
			}
		}

		a {
			width: 100%;
			display: block;
			padding: 12px;
			font-size: 12px;
			font-weight: 700;
			transition: all .3s ease;
			color: #fff;
			white-space: nowrap;
		}

	}

}

.dropdown-r {
	position: relative;

	> a {
		position: relative;

		&:before {
			content: "";
			position: absolute;
			top: 15px;
			right: 9px;
			border-right: 1px solid transparent;
			border-bottom: 1px solid transparent;
			transform: rotate(-45deg);
			width: 9px;
			height: 9px;
			border-color: #fff;

		}
	}

	&:hover {
		> ul {
			opacity: 1;
			visibility: visible;
			left: calc(100% - -1px);
		}
	}

	ul {
		left: 130%;
		right: auto;
		top: -5px;
		width: auto;
	}
}

.dropdown-top-r {

	> ul {
		right: 0;
	}

	.dropdown-r {

		&:hover {
			> ul {
				right: calc(100% - -1px);
			}
		}

		ul {
			right: 130%;
			left: auto;
		}
	}
}
