.check-box {
	input {
		display: none;


		&:checked + label {

			&:after {
				content: '';
				background: #0c304c;
				height: 8px;
				width: 8px;
				left: 3px;
				top: 5px;
			}

			&.red {&:after{background: #b53032;}}
			&.green {&:after{background: #8fac44;}}
			&.violet {&:after{background: #6b4d97;}}
			&.yellow {&:after{background: #ff8400;}}
		}
	}

	label {
		padding-left: 23px;
		position: relative;
		cursor: pointer;

		&:after {
			@extend %pseud;
			display: block;
			height: 20px;
			left: -2px;
			top: -2px;
			width: 20px;
			transition: all 0.1s linear;
		}

		&:before {
			content: '';
			background: #fff;
			display: block;
			border: 1px solid #8e98a0;
			height: 12px;
			left: 0;
			position: absolute;
			top: 2px;
			width: 12px;
		}
	}
}

