.b-info {
	@extend %clfix;
	width: 180px;
	float: left;
	padding-top: 15px;

	&__item {
		position: relative;
		float: left;
		min-width: 20%;
		height: 368px;

		&:first-child {
			margin-right: 6px;
		}

		&--right {
			.b-info-data {
				border-right-width: 1px;

				&:before {
					right: 0;
				}
				&:after {
					right: 0;
				}
			}
			.b-data__item {
				span {
					padding-right: 20px;

					&:after {
						right: 0;
					}
				}
			}
		}

		&--left {
			.b-info-data {
				border-left-width: 1px;

				&:before,
				&:after {
					left: 0;
				}
			}
			.b-data__item {
				span {
					padding-left: 20px;

					&:after {
						left: 0;
					}
				}
			}
		}

		&--red {
			.b-info-data {
				border-color: #b53032;
			}

			.b-info-data,
			.b-data__item span {
				&:before,
				&:after {
					background: #b53032;
				}
			}

			.b-info-text {
				color: #b53032;
			}
		}

		&--blue {
			.b-info-data {
				border-color: #095fa1;
			}

			.b-info-data,
			.b-data__item span {
				&:before,
				&:after {
					background: #095fa1;
				}
			}

			.b-info-text {
				color: #095fa1;
			}
		}
		&--yellow {
			.b-info-data {
				border-color: #ff8400;
			}

			.b-info-data,
			.b-data__item span {
				&:before,
				&:after {
					background: #ff8400;
				}
			}

			.b-info-text {
				color: #ff8400;
			}
		}
		&--gray {
			.b-info-data {
				border-color: #57626a;
			}

			.b-info-data,
			.b-data__item span {
				&:before,
				&:after {
					background: #57626a;
				}
			}

			.b-info-text {

				b {
					font-weight: 400;

					&:first-child {
						color: #8fac44;
					}
					&:last-child {
						color: #6b4d97;
					}
				}
				color: #57626a;
			}
		}
	}

	&-data,
	&-text {
		float: left;
	}

	&-data {
		border-style: solid;
		border-width: 0px;
		padding-top: 10px;
		padding-bottom: 32px;
		position: relative;

		&:before,
		&:after {
			@extend %pseud;
			height: 1px;
			width: 7px;
		}

		&:before {top: 0;}
		&:after {bottom: 0;}

	}

	&-text {
		position: relative;
		height: 282px;
		width: 30px;
		overflow: hidden;

		span {
			@include centerT;
			display: block;
			transform: rotate(-90deg);
			left: -10px;
		}

		&--wight1 {
			span {
				width: 79px;
				left: -26px;
			}
		}
		&--wight2 {
			span {
				width: 172px;
				left: -70px;
			}
		}
	}
}

.b-data__item {
	height: 60px;
	position: relative;
	display: table;
	color: #57626a;
	font-size: 14px;

	span {
		display: table-cell;
		vertical-align: middle;

		&:after {
			@extend %pseud;
			height: 1px;
			width: 7px;
			bottom: 0;
			top: 0;
			margin: auto;
		}
	}
}

