.box_code {
	position: relative;
	transform: translate(-50%, -50%);
	left: 50%;
	top: 10%;
	transition: all .3s ease;
	opacity: 0;
	width: 600px;
	visibility: hidden;
	background: #fafafa;

	&.visible {
		opacity: 1;
		visibility: visible;
		top: 50%;
		transition: all .3s ease;
	}

	> div {
		display: none;
	}
}

.container {
	transition: .5s filter linear;
	-webkit-transition: .5s -webkit-filter linear;
	-moz-transition: .5s -moz-filter linear;
	-ms-transition: .5s -ms-filter linear;
	-o-transition: .5s -o-filter linear;

	&.blur {
		filter: blur(2px);
		-moz-filter: blur(2px);
		-ms-filter: blur(2px);
		-o-filter: blur(2px);
	}
}

.box-conteiner {
	transition: all .3s linear;
	opacity: 0;
	visibility: hidden;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;

	&.visible {
		visibility: visible;
		opacity: 1;
		background: rgba(255,255,255,.5);
		z-index: 9999;
	}

	.box_code {
		padding: 30px;
	}

	textarea {
		width: 100%;
		height: 100px;
		margin: 0;
		resize: none;
		margin-bottom: 20px;
	}

	div[id*='code_tr'] {
		padding-bottom: 30px;
	}

