.drilldown-box {
	width: 100%;
	@extend %clfix;
	margin-bottom: 26px;

	&:last-child {
		margin-bottom: 41px;
	}

	> span {
		display: block;
		color: #8e98a0;
		font-size: 14px;
		display: block;
		margin-bottom: 7px;
	}


	.drill-select,
	.drilldown {
		float: left;
	}

	.drill-select {
		@extend %clfix;
		padding-left: 10px;

		p {
			float: left;
			color: #57626a;
			font-size: 14px;
			position: relative;
			margin-right: 7px;
			padding-bottom: 0;
			padding-top: 6px;

			&:not(:last-child) {

				&:before {
					position: absolute;
					content: ',';
					right: -5px;
					bottom: 0;
					font-size: 14px;
					color: #57626a;
				}
			}
		}
	}
}

.drilldown {
	width: 269px;

	> ul > li {
		position: relative;

		&:after {
			@extend %pseud;
			background: #fff;
			display: block;
			border: 1px solid #8e98a0;
			height: 12px;
			top: 8px;
			left: 12px;
			width: 12px;
		}
	}


//
	.selected label {

		&:after {
			content: '';
			background: #0c304c;
			height: 8px;
			width: 8px;
			left: 15px;
			top: 11px;
		}
	}

	label {
		padding-left: 23px;
		position: relative;
		cursor: pointer;

		&:after {
			@extend %pseud;
			display: block;
			height: 20px;
			left: 8px;
			top: 8px;
			width: 20px;
			transition: all 0.1s linear;
		}

		&:before {
			content: '';
			background: #fff;
			display: block;
			border: 1px solid #8e98a0;
			height: 12px;
			top: 8px;
			left: 12px;
			position: absolute;
			width: 12px;
		}
	}

//

	.title_select {
		overflow: hidden;

		&:after {
			@extend %pseud;
			display: block;
			height: 20px;
			left: 8px;
			top: 8px;
			width: 20px;
			transition: all 0.1s linear;
			z-index: 9;
		}

		&.check_values {

			&:after {
				@extend %pseud;
				background: #0c304c;
				height: 8px;
				width: 8px;
				left: 14px;
				top: 10px;
				z-index: 9;
			}
		}

		+ ul {
			display: none;
		}
	}


	li {
		cursor: pointer;

		> ul {
			z-index: 9999;
			border-bottom: 1px solid #8e98a0;
			position: absolute;
			width: 269px;
			z-index: 99;
			background: #fff;

		}

		> div.selected {
			label {
				background: #ddd;
				color: #57626a;

				&:hover {
					background: #57626a;
					color: #fff;
				}
			}
		}

		label {
			padding: 7px 10px 7px 34px;
			height: 30px;
			margin-bottom: 0px;
			cursor: pointer;
			border-left: 1px solid #8e98a0;
			border-right: 1px solid #8e98a0;

			input {
				display: none;
			}

			&:hover {
				background: #57626a;
				color: #fff;
			}
		}

	}
}


// STRANITSA_PARTNERA

.table-scroll .drilldown-box .drill-select,
.set-partnera .drilldown-box .drill-select {
	opacity: 0;
	display: none;
}

.set-partnera {

	.drilldown-box {
		width: 434px;
		float: right;

		> span,
		> .drilldown {
			display: inline-block;
			vertical-align: middle;
		}

		> span {
			float: left;
			padding-top: 5px;
		}

		> .drilldown {
			float: right;
			margin-right: 5px;
			width: 239px;

			.title_select,
			ul label,
			ul {
				width: 239px;
			}
		}
	}
}
